import React, { useEffect, useState } from "react"
import { Link } from "gatsby"

import { Container } from "./styles"

export default function RBaseHeader() {
  const [hasPass, setHasPass] = useState(false)

  useEffect(() => {
    if (typeof window !== "undefined") {
      function cb() {
        if (window.scrollY >= 100) {
          setHasPass(true)
        }
        if (window.scrollY <= 99) {
          setHasPass(false)
        }
      }
      cb()
      window.addEventListener("scroll", cb)
    }

    return () =>
      typeof window !== "undefined" && window.removeEventListener("scroll", cb)
  }, [])

  const pathname = typeof window !== "undefined" ? window.location.pathname : ""
  const noreasonif = pathname.includes("about") || pathname.includes("blog")

  return (
    <Container className={noreasonif ? "solid" : hasPass ? "solid" : ""}>
      <nav>
        <Link to="/">Início</Link>
        <Link to="/about">Sobre</Link>
        <Link to="/#segmentos">Segmentos</Link>
        <Link to="/#contato">Contato</Link>
        <Link to="/blog">Blog</Link>
      </nav>
    </Container>
  )
}
